.navbar {
    border: none;
    border-radius: 0;
    margin-bottom: 0;
    min-height: 0;
}

.navbar-header {
    padding: 65px 0 55px;
    position: relative;
}

.navbar-custom {
    background: #444;
}
@media (max-width:992px) {
    .navbar-custom {
        display: none;
    }
}

#navbar {
    font-family: $font-primary;
    font-size: 17px;
    clear: both;
    padding: 0;
}

.navbar-inverse {
    background: 0 0;
}

.navbar-inverse .navbar-nav > li > a {
    color: #fff;
    padding: 15px 15px 10px;
    z-index: 1;
}

.navbar-inverse .navbar-nav > .active > a,
.navbar-inverse .navbar-nav > .active > a:focus,
.navbar-inverse .navbar-nav > .active > a:hover {
    color: #fff;
    background: 0 0;
    position: inherit;
}

.navbar-inverse .navbar-nav > li.active:after,
.navbar-inverse .navbar-nav > li:after,
.navbar-inverse .navbar-nav > li:hover:after {
    display: block;
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 0;
    transition: 1s ease all;
    height: 3px;
    content: '';
}

.navbar-inverse .navbar-nav > li:after {
    background-color: #444;
}

.navbar-inverse .navbar-nav > li.active:after {
    background-color: #ff5722;
}

.navbar-inverse .navbar-nav > li:hover:after {
    background-color: $c_green;
}

.navbar-nav {
    float: right;
    margin: 0;
}

.navbar-toggle {
    margin-top: 0;
}

.navbar-toggle .icon-bar {
  height: 2px;
  width: 30px;
  margin-top: 7px;
  background: #fff;
}
.navbar-toggle .icon-bar+.icon-bar {
    margin-top: 7px;
}

.dropdown {
    position: relative;
}

.dropdown-toggle {
    position: absolute!important;
    top: 0;
    right: 0;
}

.top-level-link {
    padding-right: 27px !important;
}
@media (max-width:992px) {
    .navbar-header {
        float: none;
    }

    .navbar-left,
    .navbar-nav,
    .navbar-right {
        float: none!important;
    }

    .navbar-toggle {
        display: block;
    }

    .navbar-collapse {
        border-top: 1px solid transparent;
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, .1);
    }

    .navbar-fixed-top {
        top: 0;
        border-width: 0 0 1px;
    }

    .navbar-collapse.collapse {
        display: none!important;
    }

    .navbar-nav > li {
        float: none;
    }

    .navbar-nav > li > a {
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .collapse.in {
        display: block!important;
    }

    .mobile-menu .navbar-collapse {
        background: $c_dark;
    }

    .navbar-header .tagline {
        display: none;
    }

    .mobile-menu .tagline {
        float: left;
        margin: 20px 0 20px 15px;
    }

    #navbar {
        padding: 0;
    }

    .navbar-nav {
        clear: both;
        margin-top: 10px;
    }

    .nav > li > a {
        color: #fff;
    }

    .nav > li {
        background: #444;
        border-bottom: 1px solid $c_dark;
    }

    .nav .open > a,
    .nav .open > a:focus,
    .nav .open > a:hover,
    .nav > li.active.open,
    .nav > li:focus {
        background: #009f85;
    }

    .nav > li > a:focus,
    .nav > li > a:hover,
    .nav > li:hover {
        background: #444;
    }

    .navbar-nav .open .dropdown-menu > li > a {
        background: #444;
        padding: 10px 15px 10px 25px;
    }

    .navbar-nav .open .dropdown-menu > li > a:before {
        content: '';
        width: 5px;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: #009f85;
        display: block;
    }

    .nav > li > a.active:focus,
    .nav > li > a.active:hover,
    .nav > li.active,
    .nav > li.active > a:hover,
    .nav > li.active:hover {
        background: $c_main;
    }

    .navbar-header {
        padding: 10px 0;
    }

    .navbar > .container .navbar-brand {
        margin-left: 15px;
    }

    .navbar-nav .open .dropdown-menu {
        position: static;
        float: none;
        width: auto;
        margin-top: 0;
        background-color: transparent;
        border: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
    }

    .dropdown-toggle {
        position: absolute!important;
        top: 0;
        right: 0;
        border-left: solid 1px $c_dark;
    }

    .nav .open > a,
    .nav .open > a:focus,
    .nav .open > a:hover,
    .nav > li.active.open,
    .nav > li:focus {
        border-left: solid 1px $c_dark!important;
    }
}
@media (min-width:992px) {
    .dropdown-toggle {
        pointer-events: none;
    }

    .container > .navbar-header {
        margin-top: 55px;
    }

    #topContainer {
        display: none;
    }
}
@media (min-width:768px) {
    .container > .navbar-collapse,
    .container > .navbar-header {
        margin-right: 15px;
        margin-left: 15px;
    }

    .container > .navbar-header {
        width: 100%;
    }

    .navbar-inverse .navbar-collapse .tagline {
        display: none;
    }
}
