        width: 100%;
        display: block;
        background: 0 0;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999;
    }

    .animation-nav-tweak {
        padding-top: 130px !important;
    }
}
@media (max-width:479px) {
    .animation-nav#topContainer {
        width: 100%;
        display: block;
        background: 0 0;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 999;
    }

    .animation-nav-tweak {
        padding-top: 130px !important;
    }
}

