    font-size: 50px;
    color: $c_main;
    margin: 3vh 0;
    // margin-top: 5vh;
    text-transform: uppercase;
}

.hero-with-image .jumbotron h2 {
    width: 58%;
    font-family: $font-text;
    font-size: 43px;
    line-height: 56px;
    margin: 0;
}

#motion-header {
    height: 100%;
}

#motion-header .jumbotron h1 {
    width: 60%;
}

#motion-header .jumbotron h2 {
    width: 65%;
}
