        width: 55%;
        font-size: 40px;
        line-height: 48px;
        margin: 30px 0 30px;
    }

    .hero-with-image .jumbotron h2,
    .jumbotron h2 {
        font-family: $font-text;
        font-size: 28px;
        line-height: 36px;
    }

    .jumbotron h2 {
        width: 50%;
        margin: 5vw 0;
    }

    #contact .jumbotron h2 {
        width: 70%;
    }

    .hero-with-image .jumbotron h2 {
        width: 50%;
        margin: 0;
    }
}
@media (max-width:991px) {
    .hero-with-image {
        background: $c_dark;
        padding: 40px 0 80vw;
    }

    #ui-ux-header {
        background: #000;
        padding: 40px 0 40vw;
    }

    .hero-with-image .jumbotron h1 {
        width: 100%;
        font-size: 32px;
        line-height: 40px;
        margin: 30px 0 30px;
    }

    .hero-with-image .jumbotron h2,
    .jumbotron h2 {
        font-family: $font-text;
        font-size: 28px;
        line-height: 36px;
        width: 100%;
    }

    .jumbotron h2 {
        margin: 11vw 0;
    }

    .hero-with-image .jumbotron h2 {
        margin: 0;
    }

    #motion-header .jumbotron h1,
    #motion-header .jumbotron h2 {
        width: 100%;
    }
}
@media only screen and (min-width:992px) {
    body,
    html {
        height: 100%;
    }

    .navbar-header {
        padding: 0;
    }

    .logo-container {
        padding-bottom: 70px;
    }

    .hero-with-image {
        min-height: 100%;
        padding: 70px 0 100px;
    }
}

