    -moz-transform: translateX(-50%);
    transform: translateX(-50%);
}

#areas-of-expertise .btn::before,
#clients .btn::before {
    -webkit-transform: scale3d(.8, .5, 1);
    -moz-transform: scale3d(.8, .5, 1);
}

#areas-of-expertise .btn {
    background: 0 0;
    color: $c_dark;
    border: 2px solid $c_dark;
    border-radius: 0;
    margin-top: 10px;
    margin-bottom: 60px;
    padding: 15px 25px;
}
@media only screen and (max-width:480px) {
    #areas-of-expertise .btn {
        width: 100%;
        padding: 15px 25px;
        margin-bottom: 20vw;
    }
}

#areas-of-expertise .btn::before {
    content: '';
    position: absolute;
    top: -20px;
    left: -20px;
    bottom: -20px;
    right: -20px;
    background: inherit;
    z-index: -1;
    opacity: 0.4;
    transform: scale3d(.8, .5, 1);
}

#areas-of-expertise .btn:hover {
    background: $c_dark;
    color: $c_btn_white;
    transition: background 0.1s 0.3s, color 0.1s 0.3s;
    animation: anim-moema-1 0.3s 0.3s forwards;
}

#areas-of-expertise .btn:hover::before {
    animation: anim-moema-2 0.3s 0.3s forwards;
}

